<script>
export default {
  data () {
    return {
      title: 'Resume',
      name: 'Nick',
      surname: 'Fabrizio',
      address: 'Boston, Massachusetts·',
      email: 'nickfabrizio28@gmail.com',
      aboutMe: 'Virtualization & IT Automation specialist with web development knowledge.',
      experience: [
        {
          position: 'Info Sys 1',
          company: 'Raytheon',
          description: 'Maintain and develop systems that host logistics and operations applications.',
          startDate: 'June 2019',
          endDate: 'Present'
        },
        {
          position: 'Jr Engineer',
          company: 'Abacus Technology Corporation | US Air Force',
          description: 'Networking team member that participated in mananging computing environments for the USAF.',
          startDate: 'May 2018',
          endDate: 'August 2018'
        },
      ]
    }
  },
  mounted () {
    const $ = this.jquery
    this.$nextTick(() => {
      $('a.js-scroll-trigger[href*="#"]:not([href="#"])').click(function () {
        if (location.pathname.replace(/^\//, '') === this.pathname.replace(/^\//, '') && location.hostname === this.hostname) {
          var target = $(this.hash)
          target = target.length ? target : $('[name=' + this.hash.slice(1) + ']')
          if (target.length) {
            $('html, body').animate({
              scrollTop: (target.offset().top)
            }, 1000, 'easeInOutExpo')
            return false
          }
        }
      })
      $('.js-scroll-trigger').click(function () {
        $('.navbar-collapse').collapse('hide')
      })
      $('body').scrollspy({
        target: '#sideNav'
      })
    })
  }
}
</script>

